body {
  margin: 0;
  padding: 50px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  font-size: 50px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Cronometer{
    padding: 20px;
    border: 1px solid #8e9090;
    border-radius: 5px;
    text-align: center;
}

.Cronometer-Buttons button{
    padding:20px 30px;
    font-weight: 400;
    border-radius: 10px;
    border: 0px solid;
    margin: 0 20px;
    font-size: 50px;
    cursor: pointer;
}


.Cronometer-Value{
    margin-top: 20px;
    font-size: 200px;
}
